<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 14,  //当前表格的名称编号
        setting: null
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn1('26_1', 1, 25)
        this.fn1('26_2', 2, 25)
        this.fn1('26_3', 3, 25)
        this.fn1('26_4', 4, 25)
        this.fn1('26_5', 5, 25)
        this.fn1('26_6', 6, 25)
      },
      // q  需计算单元格的name
      // r  参与运算的列
      // h  要运算多少行
      // 公式:a=b+c...
      fn1(q, r, h) {
        var sum = 0
        for (let i = 1; i <= h; i++) {
          let dom = J('input[name=' + i + '_' + r + ']')
          if (dom.val()) {
            sum += parseFloat(dom.val())
          }
        }
        J('input[name=' + q + ']').val(sum.toFixed(2))
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
    text-align: center;
  }

  table {
    max-width: 900px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

  .bg-gray {
    background: #ddd;
  }

  .tdleft {
    text-align: left;
  }
</style>



